@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
/* SCSS HEX */
$eggshell: #faf3ddff;
$laurel-green: #c8d5b9ff;
$cambridge-blue: #8fc0a9ff;
$cambridge-blue-dark: rgb(109, 165, 139);
$green-sheen: #68b0abff;
$green-sheen-dark: #599692;
$amazon: #649773;
$amazon-dark: #4a855d;

$open: #f1ecc3;
$running: #c9d8b6;
$finished: #57837b;
$open-dark: $cambridge-blue-dark;
$running-dark: $green-sheen-dark;
$finished-dark: $amazon-dark;
$task-bg: $eggshell;
$font-dark: #515e63;
$time: orange;
$delete: rgb(224, 62, 62);

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Roboto", sans-serif;
  text-align: center;
  color: $font-dark;
}
.tabs {
  display: none;
  width: 100%;
}
.app {
  width: 100%;
  min-height: 100vh;
  display: flex;
  > div {
    width: 33.33%;
  }
}
.category {
  min-height: 100vh;
  padding: 0.5em;
}

//desktop headers
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $task-bg;
  padding: 0.7em 1em;
}
.number {
  font-size: 1.4em;
  font-weight: 600;
}
.header-open {
  background-color: $open-dark;
}
.open {
  background-color: $open;
}
.header-running {
  background-color: $running-dark;
}
//end of desktop headers
.running {
  background-color: $running;
}
.header-finished {
  background-color: $finished-dark;
}
.finished {
  background-color: $finished;
}

.add-form {
  text-align: left;
  margin: 1em 2em;
  div {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  input {
    font-size: 16px;
    background-color: transparent;
    width: 100%;
    margin-bottom: 0.7em;
    margin-left: 1em;
    padding: 0.5em 0.3em;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.219);
    outline: none;
  }
  input:focus {
    border-bottom-color: rgba(0, 0, 0, 0.411);
  }
  ::-webkit-input-placeholder {
    color: rgba(0, 0, 0, 0.61);
  }
  button {
    background-color: $task-bg;
    color: $font-dark;
  }
}

.task {
  background-color: $task-bg;
  padding: 1em 1.5em;
  margin: 1em;
  border-radius: 10px;
  text-align: left;
  font-weight: 600;
  h3,
  p {
    margin-bottom: 0.5em;
  }
  h3 {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .details {
    opacity: 0.6;
    line-height: 1.3;
  }
  .time {
    display: flex;
    align-items: flex-start;
    color: $time;
    font-size: 0.9em;
    margin-bottom: 0;
    margin-top: 1.5em;
    font-weight: 600;
    p {
      margin-left: 0.3em;
    }
  }
  .down-icon {
    display: inline;
    text-align: center;
    color: initial;
    background-color: transparent;
    cursor: pointer;
  }
  .down-icon:hover {
    opacity: 0.8;
  }

  .task-footer {
    margin-top: 0.5em;
    display: flex;
    justify-content: space-between;
  }
}

//mobile
@media screen and (max-width: 700px) {
  .app {
    display: none;
  }
  .tabs {
    display: block;
  }
}
.tab {
  padding: 1em 7px;
  text-align: center;
  font-weight: 600;
  .tab-header {
    align-items: center;
    display: flex;
    justify-content: space-between;
  }
}
.tab:first-child {
  background-color: $open;
  width: 32%;
}
.tab:nth-child(2) {
  background-color: $running;
  width: 34%;
}
.tab:nth-child(3) {
  color: #f1ecc3;
  background-color: $finished;
  width: 34%;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.icon {
  margin-right: 0.2em;
}
//end of mobile

//buttons
button {
  letter-spacing: 1px;
  color: $task-bg;
  padding: 0.5em 0.8em;
  font-size: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 600;
  display: block;
}
button:hover {
  opacity: 0.8;
}
button[type="submit"] {
  margin-top: 1em;
}
.delete-btn {
  background-color: $delete;
}
.finished-btn {
  background-color: $finished;
}
.stopped-btn {
  background-color: $open;
}
.select-btn {
  background-color: $running;
}
.round {
  color: $font-dark;
  display: inline-block;
  background-color: $task-bg;
  padding: 0;
  margin: 0.5em auto;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.7em;
  font-weight: 600;
}
//end of buttons
